/* global Office, Word */

const STORAGE = {
  MODE: "wc_mode",
  TARGET: "wc_target",
  RANGE_MIN: "wc_range_min",
  RANGE_MAX: "wc_range_max",
  DELTA_TARGET: "wc_delta_target",
  BASELINE: "wc_baseline",
  STATS: "wc_stats_v1",
  MILESTONES: "wc_milestones_v1",
  MOMENTUM_ENABLED: "wc_momentum_enabled"
};

const REFRESH_MS = 1000;
const MOMENTUM_WINDOW_SEC = 60;

let refreshTimer = null;

// Momentum history: [{t:number, words:number}]
let momentumHistory = [];

let momentumEnabled = false;

// Session state
let session = {
  active: false,
  startTs: null,
  startWords: null,
  added: 0,
  removed: 0
};

let lastCount = null;
let confetti = null;
let lastGlowWordCount = null;
let glowUntilTs = 0;
let glowCooldownUntilTs = 0;

Office.onReady((info) => {
  if (info.host !== Office.HostType.Word) return;

  wireUI();
  bootstrap().catch((e) => setStatus(`Init error: ${e?.message || e}`));
});

function wireUI() {
  // Drawer controls
  const openSettings = document.getElementById("openSettings");
  const closeSettings = document.getElementById("closeSettings");
  const backdrop = document.getElementById("settingsBackdrop");
  openSettings.addEventListener("click", () => toggleSettings(true));
  closeSettings.addEventListener("click", () => toggleSettings(false));
  backdrop.addEventListener("click", () => toggleSettings(false));

  // Mode + targets
  document.getElementById("saveMode").addEventListener("click", async () => {
    const mode = document.getElementById("mode").value;
    await setItem(STORAGE.MODE, mode);
    await applyModeVisibility(mode);
    await updateUI();
  });

  document.getElementById("saveTarget").addEventListener("click", async () => {
    const n = parseInt(document.getElementById("target").value, 10);
    if (!isValidPositiveInt(n)) return setStatus("Enter a valid target (> 0).");
    await setItem(STORAGE.TARGET, String(n));
    await updateUI();
  });

  document.getElementById("saveRange").addEventListener("click", async () => {
    const min = parseInt(document.getElementById("rangeMin").value, 10);
    const max = parseInt(document.getElementById("rangeMax").value, 10);
    if (!Number.isFinite(min) || min < 0) return setStatus("Range min must be 0 or higher.");
    if (!isValidPositiveInt(max)) return setStatus("Range max must be > 0.");
    if (max <= min) return setStatus("Range max must be greater than range min.");
    await setItem(STORAGE.RANGE_MIN, String(min));
    await setItem(STORAGE.RANGE_MAX, String(max));
    await updateUI();
  });

  document.getElementById("saveDelta").addEventListener("click", async () => {
    const n = parseInt(document.getElementById("deltaTarget").value, 10);
    if (!isValidPositiveInt(n)) return setStatus("Enter a valid delta target (> 0).");
    await setItem(STORAGE.DELTA_TARGET, String(n));
    await updateUI();
  });

  // Actions
  document.getElementById("refresh").addEventListener("click", async () => updateUI());

  document.getElementById("baseline").addEventListener("click", async () => {
    const wc = await getCurrentWordCount();
    await setItem(STORAGE.BASELINE, String(wc));
    setStatus(`Baseline reset to ${wc}.`);
    await updateUI();
  });

  document.getElementById("toggleMomentum").addEventListener("click", async () => {
    momentumEnabled = !momentumEnabled;
    await setItem(STORAGE.MOMENTUM_ENABLED, momentumEnabled ? "1" : "0");
    if (!momentumEnabled) {
      momentumHistory = [];
      setText("wpm", "—");
    }
    renderMomentumButton();
    await updateUI();
  });

  document.getElementById("sessionBtn").addEventListener("click", async () => {
    if (!session.active) await startSession();
    else await stopSession();
    await updateUI();
  });

  // Stats reset
  document.getElementById("resetStats").addEventListener("click", async () => {
    await setItem(STORAGE.STATS, JSON.stringify(defaultStats()));
    await renderStats();
    setStatus("Personal records reset.");
  });
}

async function bootstrap() {
  // Init persisted settings
  const mode = (await getItem(STORAGE.MODE)) || "absolute";
  document.getElementById("mode").value = mode;

  const target = await getInt(STORAGE.TARGET);
  if (target) document.getElementById("target").value = String(target);

  const rmin = await getInt(STORAGE.RANGE_MIN);
  const rmax = await getInt(STORAGE.RANGE_MAX);
  if (Number.isFinite(rmin)) document.getElementById("rangeMin").value = String(rmin);
  if (Number.isFinite(rmax)) document.getElementById("rangeMax").value = String(rmax);

  const deltaT = await getInt(STORAGE.DELTA_TARGET);
  if (deltaT) document.getElementById("deltaTarget").value = String(deltaT);

  const statsRaw = await getItem(STORAGE.STATS);
  if (!statsRaw) await setItem(STORAGE.STATS, JSON.stringify(defaultStats()));

  const msRaw = await getItem(STORAGE.MILESTONES);
  if (!msRaw) await setItem(STORAGE.MILESTONES, JSON.stringify({}));

  // Momentum enabled persisted
  const me = await getItem(STORAGE.MOMENTUM_ENABLED);
  momentumEnabled = me === "1";
  renderMomentumButton();

  await applyModeVisibility(mode);
  await renderStats();

  // Prime baseline if missing
  const baseline = await getInt(STORAGE.BASELINE);
  if (!Number.isFinite(baseline)) {
    const wc = await getCurrentWordCount().catch(() => null);
    if (Number.isFinite(wc)) await setItem(STORAGE.BASELINE, String(wc));
  }

  confetti = new Confetti(document.getElementById("confetti"));

  await updateUI();
  startAutoRefresh();
}

function startAutoRefresh() {
  if (refreshTimer) clearInterval(refreshTimer);
  refreshTimer = setInterval(() => updateUI().catch(() => {}), REFRESH_MS);
}

function toggleSettings(show) {
  document.getElementById("settingsDrawer").classList.toggle("hidden", !show);
  document.getElementById("settingsBackdrop").classList.toggle("hidden", !show);
}

function renderMomentumButton() {
  const btn = document.getElementById("toggleMomentum");
  btn.textContent = momentumEnabled ? "Stop momentum" : "Start momentum";
}

function isValidPositiveInt(n) {
  return Number.isFinite(n) && n > 0;
}

async function applyModeVisibility(mode) {
  const targetRow = document.getElementById("targetRow");
  const rangeRow = document.getElementById("rangeRow");
  const deltaRow = document.getElementById("deltaRow");

  targetRow.classList.add("hidden");
  rangeRow.classList.add("hidden");
  deltaRow.classList.add("hidden");

  if (mode === "absolute") targetRow.classList.remove("hidden");
  if (mode === "range") rangeRow.classList.remove("hidden");
  if (mode === "delta") deltaRow.classList.remove("hidden");
}

async function getCurrentWordCount() {
  return Word.run(async (context) => {
    const body = context.document.body;
    body.load("text");
    await context.sync();
    return countWords(body.text || "");
  });
}

function countWords(text) {
  const cleaned = (text || "")
    .replace(/\u00A0/g, " ")
    .replace(/[^\S\r\n]+/g, " ")
    .trim();
  if (!cleaned) return 0;

  const tokens = cleaned.split(/\s+/);
  const words = tokens.filter((t) => /[A-Za-z0-9À-ÖØ-öø-ÿ]/.test(t));
  return words.length;
}

function nowMs() {
  return Date.now();
}

function clamp(n, a, b) {
  return Math.max(a, Math.min(b, n));
}

async function updateUI() {
  const mode = (await getItem(STORAGE.MODE)) || "absolute";
  const current = await getCurrentWordCount();
  // Trigger bar-end glow when words increase, with 1s cooldown
  const now = nowMs();
  if (lastGlowWordCount == null) lastGlowWordCount = current;

  const increased = current > lastGlowWordCount;
  if (increased && now >= glowCooldownUntilTs) {
    glowUntilTs = now + 450;          // glow visible duration
    glowCooldownUntilTs = now + 1000; // 1 second cooldown
  }

  lastGlowWordCount = current;

  // Session edit tracking
  trackSessionEdits(current);

  // Momentum tracking only if enabled
  if (momentumEnabled) pushHistory(current);

  const computed = await computeProgress(mode, current);

  // Main panel
  setText("currentWords", String(current));
  setText("targetWords", computed.targetText);
  setText("percent", computed.percentText);
  setText("targetLabel", computed.targetLabel);

  renderProgressBar(computed);
  renderWpm();

  // Status line
  setStatus(computed.statusLine);

  // Milestones (optional delight)
  await handleMilestones(mode, current, computed);

  // Update settings-side stats
  await renderStats();
}

function setText(id, v) {
  const el = document.getElementById(id);
  if (el) el.textContent = v;
}

function setStatus(msg) {
  const el = document.getElementById("status");
  if (el) el.textContent = msg;
}

function renderProgressBar(computed) {
  const bar = document.getElementById("bar");
  if (!bar) return;

  bar.style.width = `${computed.visualPercent.toFixed(1)}%`;
  bar.style.backgroundColor = computed.barColor;

  // End-glow effect while adding words (with cooldown)
  const now = nowMs();
  bar.classList.toggle("adding", now < glowUntilTs);
}


function renderWpm() {
  const el = document.getElementById("wpm");
  if (!el) return;

  if (!momentumEnabled) {
    el.textContent = "—";
    return;
  }

  const wpm = computeWordsPerMinute();
  if (!Number.isFinite(wpm)) el.textContent = "…";
  else el.textContent = wpm.toFixed(1);
}

/* ---------------------------
   Momentum (defensive)
---------------------------- */
function pushHistory(words) {
  const t = Math.floor(nowMs() / 1000);

  // Hard guard: if anything overwrote it, restore to array
  if (!Array.isArray(momentumHistory)) momentumHistory = [];

  // Clean any invalid entries (defensive)
  for (let i = momentumHistory.length - 1; i >= 0; i--) {
    const it = momentumHistory[i];
    if (!it || typeof it.t !== "number" || typeof it.words !== "number") {
      momentumHistory.splice(i, 1);
    }
  }

  const last = momentumHistory.length ? momentumHistory[momentumHistory.length - 1] : null;

  if (last && last.t === t) {
    last.words = words;
  } else {
    momentumHistory.push({ t, words });
  }

  while (momentumHistory.length) {
    const first = momentumHistory[0];
    if (!first || typeof first.t !== "number") {
      momentumHistory.shift();
      continue;
    }
    if (t - first.t <= MOMENTUM_WINDOW_SEC) break;
    momentumHistory.shift();
  }
}

function computeWordsPerMinute() {
  if (!Array.isArray(momentumHistory) || momentumHistory.length < 2) return NaN;

  const first = momentumHistory[0];
  const last = momentumHistory[momentumHistory.length - 1];
  if (!first || !last || typeof first.t !== "number" || typeof last.t !== "number") return NaN;

  const dt = last.t - first.t;
  if (dt <= 0) return NaN;

  const dw = last.words - first.words;
  return (dw / dt) * 60;
}


/* ---------------------------
   Session
---------------------------- */
function trackSessionEdits(currentWords) {
  if (!session.active) {
    lastCount = currentWords;
    return;
  }

  if (lastCount == null) {
    lastCount = currentWords;
    return;
  }

  const delta = currentWords - lastCount;
  if (delta > 0) session.added += delta;
  if (delta < 0) session.removed += Math.abs(delta);

  lastCount = currentWords;
}

async function startSession() {
  const wc = await getCurrentWordCount();
  session = {
    active: true,
    startTs: nowMs(),
    startWords: wc,
    added: 0,
    removed: 0
  };

  document.getElementById("sessionBtn").textContent = "Stop session";
  setStatus("Session started.");
}

async function stopSession() {
  const durMs = nowMs() - session.startTs;
  const net = session.added - session.removed;
  const pace = durMs > 0 ? net / (durMs / 60000) : NaN;

  await updateRecords({ netWords: net, paceWpm: pace, durationMs: durMs });

  session.active = false;
  document.getElementById("sessionBtn").textContent = "Start session";

  setStatus(`Session complete: +${session.added} −${session.removed} (net ${net >= 0 ? "+" : ""}${net}).`);
}

/* ---------------------------
   Progress computation
---------------------------- */
async function computeProgress(mode, current) {
  let targetLabel = "Target";
  let targetText = "—";
  let percentText = "—";
  let statusLine = "Set a target to see progress.";
  let visualPercent = 0;
  let pct = 0;

  if (mode === "absolute") {
    const target = await getInt(STORAGE.TARGET);
    if (!isValidPositiveInt(target)) {
      return decorateComputed({ mode, targetLabel, targetText, percentText, statusLine, visualPercent, pct });
    }
    targetText = String(target);
    pct = (current / target) * 100;
    visualPercent = clamp(pct, 0, 100);
    percentText = `${pct.toFixed(1)}%`;
    statusLine = pct < 100 ? `${Math.max(0, target - current)} words remaining.` : `Over target by ${current - target}.`;
    return decorateComputed({ mode, targetLabel, targetText, percentText, statusLine, visualPercent, pct });
  }

  if (mode === "range") {
    const min = await getInt(STORAGE.RANGE_MIN);
    const max = await getInt(STORAGE.RANGE_MAX);
    targetLabel = "Range";
    if (!Number.isFinite(min) || !isValidPositiveInt(max) || max <= min) {
      statusLine = "Set a valid range (min < max).";
      return decorateComputed({ mode, targetLabel, targetText, percentText, statusLine, visualPercent, pct });
    }
    targetText = `${min}–${max}`;

    if (current < min) {
      visualPercent = clamp((current / Math.max(1, min)) * 100, 0, 100);
      percentText = `${visualPercent.toFixed(1)}%`;
      statusLine = `Below range by ${min - current}.`;
      pct = visualPercent;
      return decorateComputed({ mode, targetLabel, targetText, percentText, statusLine, visualPercent, pct });
    }

    if (current <= max) {
      const band = max - min;
      const inBand = current - min;
      pct = (inBand / band) * 100;
      visualPercent = clamp(pct, 0, 100);
      percentText = `In range (${visualPercent.toFixed(1)}%)`;
      statusLine = `${max - current} words until max.`;
      return decorateComputed({ mode, targetLabel, targetText, percentText, statusLine, visualPercent, pct });
    }

    visualPercent = 100;
    percentText = `Over (${current - max})`;
    statusLine = `Above range by ${current - max}.`;
    pct = 100;
    return decorateComputed({ mode, targetLabel, targetText, percentText, statusLine, visualPercent, pct });
  }

  if (mode === "delta") {
    targetLabel = "Delta";
    const deltaTarget = await getInt(STORAGE.DELTA_TARGET);
    const baseline = await getInt(STORAGE.BASELINE);

    if (!isValidPositiveInt(deltaTarget) || !Number.isFinite(baseline)) {
      targetText = deltaTarget ? `+${deltaTarget}` : "—";
      statusLine = "Set delta target and baseline.";
      return decorateComputed({ mode, targetLabel, targetText, percentText, statusLine, visualPercent, pct });
    }

    const gained = current - baseline;
    targetText = `+${deltaTarget}`;
    pct = (gained / deltaTarget) * 100;
    visualPercent = clamp(pct, 0, 100);
    percentText = `${pct.toFixed(1)}%`;
    statusLine = pct < 100 ? `${Math.max(0, deltaTarget - gained)} words remaining.` : `Over delta by ${gained - deltaTarget}.`;
    return decorateComputed({ mode, targetLabel, targetText, percentText, statusLine, visualPercent, pct });
  }

  if (mode === "draft") {
    targetLabel = "Draft";
    const baseline = await getInt(STORAGE.BASELINE);
    if (!Number.isFinite(baseline)) {
      targetText = "Additions";
      percentText = "Draft";
      statusLine = "Reset baseline to count additions.";
      return decorateComputed({ mode, targetLabel, targetText, percentText, statusLine, visualPercent, pct });
    }
    const additions = Math.max(0, current - baseline);
    targetText = `${additions} added`;
    percentText = "Draft";
    visualPercent = clamp((additions / 1000) * 100, 0, 100);
    pct = visualPercent;
    statusLine = additions < 200 ? "Drafting: keep moving." : "Drafting: good output.";
    return decorateComputed({ mode, targetLabel, targetText, percentText, statusLine, visualPercent, pct });
  }

  return decorateComputed({ mode, targetLabel, targetText, percentText, statusLine, visualPercent, pct });
}

function decorateComputed(c) {
  const barColor = getColourForPercent(c.mode, c.pct);
  return { ...c, barColor };
}

function getColourForPercent(mode, pct) {
  // default thresholds
  if (mode === "draft") return pct < 30 ? "#4aa3ff" : "#39d98a";

  if (pct < 50) return "#ff4d4d";
  if (pct < 80) return "#ffb020";
  if (pct < 100) return "#4aa3ff";
  return "#39d98a";
}

/* ---------------------------
   Milestones (kept simple)
---------------------------- */
async function handleMilestones(mode, current, computed) {
  // Only do milestone effects for absolute/delta/range when reaching 100%
  if (!confetti) return;
  if (!(mode === "absolute" || mode === "delta" || mode === "range")) return;

  const pct = computed.pct;
  if (!(pct >= 100)) return;

  const ms = JSON.parse((await getItem(STORAGE.MILESTONES)) || "{}");
  const key = `${mode}|pct_100`;
  if (ms[key]) return; // already celebrated

  ms[key] = nowMs();
  await setItem(STORAGE.MILESTONES, JSON.stringify(ms));
  confetti.burst(1100);
}

/* ---------------------------
   Stats
---------------------------- */
function defaultStats() {
  return { bestNet: 0, bestPace: 0, bestDuration: 0 };
}

async function getStats() {
  const raw = await getItem(STORAGE.STATS);
  try {
    return raw ? JSON.parse(raw) : defaultStats();
  } catch {
    return defaultStats();
  }
}

function formatDuration(ms) {
  const totalSec = Math.max(0, Math.floor(ms / 1000));
  const m = Math.floor(totalSec / 60);
  const s = totalSec % 60;
  return `${m}m ${String(s).padStart(2, "0")}s`;
}

async function updateRecords({ netWords, paceWpm, durationMs }) {
  const s = await getStats();
  let changed = false;

  if (Number.isFinite(netWords) && netWords > (s.bestNet || 0)) {
    s.bestNet = netWords;
    changed = true;
  }
  if (Number.isFinite(paceWpm) && paceWpm > (s.bestPace || 0)) {
    s.bestPace = paceWpm;
    changed = true;
  }
  if (Number.isFinite(durationMs) && durationMs > (s.bestDuration || 0)) {
    s.bestDuration = durationMs;
    changed = true;
  }

  if (changed) await setItem(STORAGE.STATS, JSON.stringify(s));
}

async function renderStats() {
  const s = await getStats();
  setText("bestNet", Number.isFinite(s.bestNet) ? `${s.bestNet}` : "—");
  setText("bestPace", Number.isFinite(s.bestPace) ? `${s.bestPace.toFixed(1)} wpm` : "—");
  setText("bestDuration", Number.isFinite(s.bestDuration) ? formatDuration(s.bestDuration) : "—");
}

/* ---------------------------
   Storage helpers
---------------------------- */
async function getItem(key) {
  try {
    if (typeof OfficeRuntime !== "undefined" && OfficeRuntime.storage) {
      return await OfficeRuntime.storage.getItem(key);
    }
  } catch (_) {}
  return localStorage.getItem(key);
}

async function setItem(key, value) {
  try {
    if (typeof OfficeRuntime !== "undefined" && OfficeRuntime.storage) {
      await OfficeRuntime.storage.setItem(key, value);
      return;
    }
  } catch (_) {}
  localStorage.setItem(key, value);
}

async function getInt(key) {
  const v = await getItem(key);
  const n = parseInt(v, 10);
  return Number.isFinite(n) ? n : null;
}

/* ---------------------------
   Confetti (lightweight)
---------------------------- */
class Confetti {
  constructor(canvas) {
    this.canvas = canvas;
    this.ctx = canvas.getContext("2d");
    this.particles = [];
    this.running = false;
    this.resize();
    window.addEventListener("resize", () => this.resize());
  }

  resize() {
    this.canvas.width = window.innerWidth;
    this.canvas.height = window.innerHeight;
  }

  burst(durationMs = 900) {
    this.canvas.style.display = "block";
    const start = nowMs();

    const count = 120;
    const w = this.canvas.width;
    const h = this.canvas.height;

    for (let i = 0; i < count; i++) {
      this.particles.push({
        x: w * (0.2 + Math.random() * 0.6),
        y: h * (0.15 + Math.random() * 0.2),
        vx: (Math.random() - 0.5) * 6,
        vy: (Math.random() - 1.2) * 7,
        g: 0.18 + Math.random() * 0.10,
        size: 3 + Math.random() * 4,
        rot: Math.random() * Math.PI,
        vr: (Math.random() - 0.5) * 0.2,
        life: 600 + Math.random() * 400
      });
    }

    if (!this.running) {
      this.running = true;
      const tick = () => {
        const t = nowMs();
        this.step(16);
        this.draw();

        this.particles = this.particles.filter((p) => p.life > 0);
        if (t - start < durationMs || this.particles.length) {
          requestAnimationFrame(tick);
        } else {
          this.running = false;
          this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
          this.canvas.style.display = "none";
        }
      };
      requestAnimationFrame(tick);
    }
  }

  step(dt) {
    for (const p of this.particles) {
      p.life -= dt;
      p.vy += p.g;
      p.x += p.vx;
      p.y += p.vy;
      p.rot += p.vr;
      p.vx *= 0.99;
      p.vy *= 0.99;
    }
  }

  draw() {
    const ctx = this.ctx;
    ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
    ctx.save();
    ctx.globalAlpha = 0.9;

    for (const p of this.particles) {
      ctx.save();
      ctx.translate(p.x, p.y);
      ctx.rotate(p.rot);
      ctx.fillStyle = `rgba(255,255,255,${0.35 + Math.random() * 0.35})`;
      ctx.fillRect(-p.size / 2, -p.size / 2, p.size, p.size * 0.65);
      ctx.restore();
    }
    ctx.restore();
  }
}
